<template>
    <div style="width: 100%; height: 100%;display: flex;">
        <div class="leftt">
            <p>超级企业列表({{ num }})</p>
            <div>
                <el-input v-model="search" placeholder="请输入内容" style="width: 150px;margin: 10px;"></el-input>
                <el-button type="primary" @click="dialogFormVisible = true"
                    style="width: 40px;padding: 5px 5px;background-color: #E9F3FB;color: aqua;font-size: 25px;"><i
                        class="el-icon-plus"></i></el-button>
                <el-dialog :visible.sync="dialogFormVisible" title="新增超级企业" style="width: 1000px;margin: 0 auto;"
                    :lockScroll="false">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
                        <el-form-item label="超级企业名称" prop="ename">
                            <el-input v-model="ruleForm.ename" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="企业域名" prop="ipstr">
                            <el-input v-model="ruleForm.ipstr" style="width: 270px;">
                                <template slot="append">.emicloud.com</template>
                            </el-input>
                        </el-form-item>
                        <el-form-item label="超级管理员账户" prop="adminname">
                            <el-input v-model="ruleForm.adminname" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="邮箱" prop="adminemil">
                            <el-input v-model="ruleForm.adminemil" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="最大坐席数" prop="maxnum">
                            <el-input v-model="ruleForm.maxnum" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="最大登录并发总数" prop="maxconcurrency">
                            <el-input v-model="ruleForm.maxconcurrency" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                            <el-button @click="dialogFormVisible = false">取消创建</el-button>
                        </el-form-item>
                    </el-form>
                </el-dialog>
            </div>
            <el-table
                :data="tableData.filter(data => !search || data.ename.toLowerCase().includes(search.toLowerCase()))"
                :show-header=false style="width: 100%;color: #91A1A9;" height="570px" @row-click="handleEdit">
                <el-table-column label="日期" width="20">
                    <template slot-scope="scope">
                        <div style="width: 10px;height: 10px;border-radius: 50%;"
                            :class="scope.row.status == 1 ? 'lan' : 'bai'"></div>
                    </template>
                </el-table-column>
                <el-table-column label="name" prop="ename">
                </el-table-column>
            </el-table>
        </div>
        <div style="width: 100%;padding: 20px;box-sizing: border-box;">
            <div class="rightt" style="width: 100%;background-color: white;box-sizing: border-box;padding: 20px;">
                <el-radio-group v-model="url">
                    <el-radio-button label="/about/qygl/one"> 超级企业管理</el-radio-button>
                    <el-radio-button label="/about/qygl/tow">子企业管理</el-radio-button>
                </el-radio-group>
                <div>
                    <router-view @child-event="parentMethod"></router-view>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getuser, addli } from "../../api/https";

export default {
    data() {
        return {
            num: 0,
            tableData: [],
            search: '',
            url: this.$route.path,
            dialogFormVisible: false,
            ruleForm: {
                ename: '',
                ipstr: '',
                adminname: '',
                adminemil: '',
                maxnum: '',
                maxconcurrency: ''
            },
            rules: {
                ename: [
                    {
                        required: true, message: '请输入超级企业名称', trigger: 'blur'
                    },
                ],
                ipstr: [
                    {
                        required: true, message: '请输入企业域名', trigger: 'blur'
                    },
                ],
                adminname: [
                    {
                        required: true, message: '请输入超级管理员账号', trigger: 'blur'
                    },
                ],
                adminemil: [
                    {
                        required: true, message: '请输入邮箱', trigger: 'blur'
                    },
                ],
            }
        }
    },

    watch: {
        url() {
            this.$router.replace(this.url)
        }
    },
    mounted() {
        this.get()
    },
    methods: {
        parentMethod() {
            this.get()
            this.get()
        },
        // 渲染页面
        get() {
            getuser()
                .then((response) => {
                    this.num = response.data.data.length
                    this.tableData = response.data.data
                })
                .catch((error) => {
                    console.error(error);
                });
        },
        //路由穿id
        handleEdit(index) {
            if (this.$route.path + '?' + Object.keys(this.$route.query) != this.url + '?' + index.id) {
                this.$router.replace(this.url + '?' + index.id)
            }
        },

        //提交
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    addli(this.ruleForm)
                        .then((response) => {
                            console.log(response)
                            this.get()
                            this.dialogFormVisible = false
                            this.$message({
                                type: 'success',
                                message: '添加成功!'
                            });
                        })
                        .catch((error) => {
                            console.error(error);
                        });
                } else {
                    this.dialogFormVisible = false
                    return false;
                }
            });
        },
    }
}
</script>

<style scoped>
.bai {
    background-color: #adadad;
}

.lan {
    background-color: #6ee982;
}

.leftt {
    width: 280px;
    background-color: white;
}

.leftt p {
    padding: 0;
    margin: 0;
    margin-left: 20px;
    font-size: 20px;
    line-height: 50px;
}
</style>